import React,{Fragment,Component} from 'react';
import '../../../style/base.css';
import $ from 'jquery';
//  ECharts 
import 'echarts/lib/chart/line';  //line,pie,bar
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/markPoint';
import ReactEcharts from 'echarts-for-react';
import axios from "axios";
import {Common} from "../../Cesium/method";
import avatorUrl from "../../../images/RenIcon.png";
import moment from "moment";



class Surveillance extends Component{
    constructor(props) {
        super(props);
        this.state={
            Date:[],
            Number:[],
            alarmNumber:[],
            startTime:'',//
            endTime:'',//
            renFaceImg:avatorUrl,//
            RenType:0,//
            RenName:'',//
            RenID:'',//
            RenList:[],//
            RouteTable:[],//
            RouteTable1:[],//
            Similarity:90,//
        }
        Surveillance.this= this;
        this.fileInputEl = React.createRef();//
        this.onAlarm = this.onAlarm.bind(this); //
        this.RenButClick = this.RenButClick.bind(this);//
        this.TimeSerch = this.TimeSerch.bind(this);//
        this.RenClose = this.RenClose.bind(this);//
        this.faceDatabase = this.faceDatabase.bind(this);//
        this.RenIdSearch = this.RenIdSearch.bind(this);//
        this.onTable = this.onTable.bind(this);//
        this.RenVideo = this.RenVideo.bind(this);//
        this.PDF = this.PDF.bind(this); //
    }
    time(){
        var D=moment().format("YYYY.MM.DD HH:mm:ss");
        var SD=moment().format("YYYY.MM.DD");
        Surveillance.this.setState({startTime:SD+' 00:00:00',endTime:D})
    }
    //
    onAlarm(){
        $(".Two").toggle();
        $(".TwoAlarm").toggle();
        if($(".Two").is(':visible')){//nodenode
            axios.post(global.Url+'event/info/statisticsForDay',{day:30}).then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    var A=[];
                    var B=[];
                    for(var i=0;i<result.length;i++){
                        A.push(result[i].date);
                        B.push(result[i].count);
                    }
                    this.setState({
                        Date:A,
                        Number:B
                    })
                }else{
                    //alert(res.msg)
                    $(".PageShow").show().find('h1').html(res.data.msg);
                    global.Time();
                }
            })
        }else{


        }

    }
    //
    RenButClick(){
        $(".RenButBut").hide();
        $(".RenSearch").show();
    }
    componentDidMount() {
        this.time();
        //roamingDisplay
        window.receiveMessageFromIndex=function(e){
            if(e!==undefined){
                //  console.log('react,iframeID',e.data);
                switch(e.data.switchName){
                    case'TrackTracking':
                        console.log(e.data);
                        if(e.data.Personnel.state===true){
                            $(".IndexHome").css({'width':'100%','top':'0','zIndex':'100'});
                        }else{
                            $(".IndexHome").css({'width':'100%','top':'100px','zIndex':'0'});
                        }

                        break;
                    default:
                        return null;
                }

            }
        }
/*        //message
        window.addEventListener("message",window.receiveMessageFromIndex,false);
        let baseUrl = global.WebSocketUrl;
        let webSocket = new WebSocket(baseUrl);
        webSocket.onopen = function (e) {
            //console.log('webSocket is open');
            webSocket.send('subscribe')
        }
        webSocket.onmessage = function (e) {
          //  var Data=JSON.parse(e.data);
          //  console.log(e.data,Data);
            if(e.data === 'subscribe success'){
                return
            }else{
                var listNumber=[];
                listNumber.push(JSON.parse(e.data));

                Surveillance.this.setState({
                    alarmNumber:listNumber
                })
                //global.WebList.push(JSON.parse(e.data));
                console.log(Surveillance.this.state.alarmNumber);
                Common.AlarmBox(JSON.parse(e.data));
                //let arr=global.WebList;
/!*                for (let i = 0; i < arr.length; i++) {
                    for (let j =i+1; j <arr.length; ) {
                        if (arr[i].event_info.id === arr[j].event_info.id) {
                            arr.splice(j, 1);
                        }
                        else j++;
                    }
                }
                if(global.AlarmShow){
                    console.log(arr);
                    for(let i=0; i<arr.length; i++){
                        Common.AlarmBox(arr[i]);
                    }
                }*!/
            }
        }*/
    }

    getOption =()=> {
        const {Date,Number} =this.state;
        let option = {
            tooltip:{
                trigger:'axis',
            },
            xAxis:{
                data:Date, //['','']
                axisLine: {
                    lineStyle: {
                        color: '#fff'
                    }
                },
            },
            yAxis:{
                type:'value',
                axisLine: {
                    lineStyle: {
                        color: '#fff'
                    }
                },
            },
            series:[
                {
                    name:'',
                    type:'line',   //typebar,pie
                    data:Number,
                    areaStyle: {
                        normal: {
                            color: '#8cd5c2' //
                        }
                    },
                }
            ]
        }
        return option
    }
    //
    inputStartChange(e){
        Surveillance.this.setState({
            startTime:e.target.value
        })
    }
    //
    inputEndChange(e){
        Surveillance.this.setState({
            endTime:e.target.value
        })
    }
    //
    SimilarityChange(e){
        Surveillance.this.setState({
            Similarity:e.target.value
        })
    }
    //
    handlePhoto = async (event) => {
        var imgFile;
        let reader = new FileReader();     //html5
        reader.readAsDataURL(event.target.files[0]);
        var that=this;
        reader.onload=function(event) {        //
            imgFile = event.target.result;
            that.setState({
                renFaceImg:imgFile,
                RenType:1
            })

        }
    }
    //
    RenClose(item){
        if(item===1){
            $(".RenButBut").show();
            $(".RenSearch").hide();
            $(".faceDatabase").hide();
            Common.closeHistoricalTrack();
        }else{
            $(".faceDatabase").hide();
        }

    }
    //
    faceDatabase(){
        $(".faceDatabase").show();
        axios.post(global.Url+'business/selectFacebank',{name:'',certificateNum:'',pageNo:1,pageSize:1000}).then((res) => {
            const result = res.data.data;
            if(result) {
                console.log(result);
                setTimeout(function(){
                    Surveillance.this.setState({
                        RenList:result
                    })
                },0)

            }else{
                $(".PageShow").show().find('h1').html(res.data.msg);
                global.Time();
            }
        })
    }
    //
    renNameChange(e){
        Surveillance.this.setState({
            RenName:e.target.value
        })
    }
    //
    renIDChange(e){
        Surveillance.this.setState({
            RenID:e.target.value
        })
    }
    //
    RenIdSearch(item){
        var data;
        if(item===1){
            const {RenName,RenID}= this.state;
            data={
                name:RenName,
                certificateNum:RenID,
                pageNo:1,pageSize:1000
            }
        }else{
            data={
                name:'',
                certificateNum:'',
                pageNo:1,pageSize:1000
            }
        }
        axios.post(global.Url+'business/selectFacebank',data).then((res) => {
            const result = res.data.data;
            if(result) {
                console.log(result);
                setTimeout(function(){
                    Surveillance.this.setState({
                        RenList:result
                    })
                },0)
            }else{
                $(".PageShow").show().find('h1').html(res.data.msg);
                global.Time();
            }
        })
    }
    //
    onTable(ImgUrl){
        setTimeout(function(){
            Surveillance.this.setState({
                renFaceImg:ImgUrl,
                RenType:2,
            })
        },0)
        $(".faceDatabase").hide();
    }
    //
    TimeSerch(){
        const {renFaceImg,startTime,endTime,RenType,Similarity} = this.state;
        if(startTime>endTime){
            $(".PageShow").show().find('h1').html('');
            global.Time();
        }else{
            var data;
            if(RenType===0){
                $(".PageShow").show().find('h1').html('');
                global.Time();
                return
            }else if(RenType===1){
              console.info("startTime",startTime)
                console.info("endTime",endTime)
                data={
                    minSimilarity:Similarity, //1
                    startTime:startTime,  //
                    endTime:endTime, //    2020-06-08T16:37:26.264Z
                    facePicUrl:'', // http
                    facePicBinaryData:renFaceImg//  base64
                }
            }else if(RenType===2){
                data={
                    minSimilarity:Similarity, //1
                    startTime:startTime,  //
                    endTime:endTime, //    2020-06-08T16:37:26.264Z
                    facePicUrl:renFaceImg, // http
                    facePicBinaryData:''//  base64
                }
            }
            console.log(data);
            axios.post(global.Url+'business/SearchImg',data).then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    setTimeout(function(){
                        Surveillance.this.setState({
                            RouteTable:result.exist,
                            RouteTable1:result
                        })
                        Common.historicalTrack(result);
                    },0);
                    $(".faceButton1").show();
                }else{
                    $(".PageShow").show().find('h1').html(res.data.msg);
                    global.Time();
                }
            })
        }
    }
    //
    PDF(){
        Common.downloadPDFV2(Surveillance.this.state.RouteTable1);
    }
    //
    RenVideo(code,time){
        //
        var data={
            camera:[
                {
                    code:code,
                    time:time
                }
            ],
        }
        Common.playback(data)
    }
    render(){
        const {renFaceImg,startTime,endTime,RenName,RenID,RenList,RouteTable,Similarity} = this.state;
        return(
            <Fragment>
                <div className="AlarmButton TwoAlarm" onClick={()=>this.onAlarm()}></div>
                <div className="AlarmShow Two" style={{'display':'none'}}>
                    <h1><span>(7)</span></h1>
                    <div className="AlarmContent">
                        <ReactEcharts option={this.getOption()} style={{height: '300px','marginTop':'-20px'}} className='react_for_echarts' />
                    </div>
                    <div className="But" onClick={()=>this.onAlarm()}></div>
                </div>
                <div className="RenBut">
                    <div className="RenButBut"><button onClick={()=>this.RenButClick()}></button></div>
                    <div className="RenSearch">
                        <div className="RenTop">
                            <h1> <i onClick={()=>this.RenClose(1)}></i></h1>
                            <div className="RenImg">
                                <input type="file" ref={this.fileInputEl} accept=".jpg,.jpeg,.png"	hidden onChange={(event) => this.handlePhoto(event)}/>
                                <img src={renFaceImg} alt=""/>
                            </div>
                            <div className="faceButtonArea">
                                <button className="faceUpload" onClick={() => {this.fileInputEl.current.click()}}></button>
                                <button className="faceStore" onClick={()=>this.faceDatabase()}></button>
                            </div>
                            <div className="faceTimeStamp">
                                <li><span></span><input type="text" onChange={(e)=>this.SimilarityChange(e)} defaultValue={Similarity} />%</li>
                                <li><span></span><input type="data" onChange={(e)=>this.inputStartChange(e)} defaultValue={startTime}/></li>
                                <li><span></span><input type="data" onChange={(e)=>this.inputEndChange(e)} defaultValue={endTime}/></li>
                            </div>
                            <div className="faceButton" onClick={()=>this.TimeSerch()}></div>
                            <div className="faceButton1" onClick={()=>this.PDF()}></div>
                        </div>
                        <div className="RenTable">
                            <table>
                                <thead>
                                   <tr>
                                       <td></td>
                                       <td></td>
                                       {/*<td></td>*/}
                                   </tr>
                                </thead>
                                <tbody>
                                    {
                                        RouteTable.length > 0 && RouteTable.map((ele,index)=>{
                                            return(
                                                <tr key={index}>
                                                    <td>{index + 1}</td>
                                                    <td>{ele.cameraName}</td>
                                                   {/* <td><button onClick={()=>this.RenVideo(ele.device_code,ele.captureTime)}></button></td>*/}
                                                </tr>
                                            )
                                        })
                                    }
                                    {RouteTable.length < 0?<div style={{'text-align':'center'}}></div>:null}
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div className="faceDatabase">
                        <h1> <i onClick={()=>this.RenClose(2)}></i></h1>
                        <div className="faceDatabaseCont">
                            <div className="ContTitle">
                                <div className="ContTitleLeft">
                                    <span></span><input type="text" onChange={(e)=>this.renNameChange(e)} defaultValue={RenName}/>
                                    <span></span><input type="text" onChange={(e)=>this.renIDChange(e)} defaultValue={RenID}/>
                                </div>
                                <div className="ContTitleRight">
                                    <span onClick={()=>this.RenIdSearch(1)}><i></i></span>
                                    <span onClick={()=>this.RenIdSearch(2)}><i></i></span>
                                </div>
                            </div>
                            <div className="ContList">
                                {
                                    RenList.length > 0 && RenList.map((ele,index)=>{
                                        return(
                                            <li key={index} onClick={()=>this.onTable(ele.facePic.faceUrl)}>
                                                <img src={ele.facePic.faceUrl} alt=""/>
                                                <p><span></span>{ele.faceInfo.name}</p>
                                                <p><span></span>{ele.faceInfo.sex==='female'?'':''}</p>
                                                <p><span></span></p>
                                                <p>{ele.faceInfo.certificateNum}</p>
                                            </li>
                                        )
                                    })
                                }
                                {RenList.length < 0?<div style={{'text-align':'center','color':'#01a1ff'}}></div>:null}
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default Surveillance;